import React, { ChangeEvent } from 'react';
import SuperSelect from '../../../hw07/common/c5-SuperSelect/SuperSelect';
import { Pagination } from '@mui/material';
import s from './SuperPagination.module.css';

export type SuperPaginationPropsType = {
  id?: string;
  page: number;
  itemsCountForPage: number;
  totalCount: number;
  onChange: (page: number, count: number) => void;
};

const SuperPagination: React.FC<SuperPaginationPropsType> = ({
  page,
  itemsCountForPage,
  totalCount,
  onChange,
  id = 'hw15',
}) => {
  const lastPage = Math.trunc(totalCount / itemsCountForPage); //   //   

  const onChangeCallback = (event: any, page: number) => {
    onChange(page, itemsCountForPage);
    // const newPage = onChangeSelect(event as ChangeEvent<HTMLSelectElement>);
    // onChange(page, Number(newPage));
    // console.log(event.currentTarget.value);
    // console.log(event);
    // console.log(page);
    //  
  };

  const onChangeSelect = (event: ChangeEvent<HTMLSelectElement>) => {
    onChange(page, Number(event.currentTarget.value));
    console.log(event.currentTarget.value);
    return event.currentTarget.value;
    //  
  };

  return (
    <div className={s.pagination}>
      <Pagination
        id={id + '-pagination'}
        sx={
          {
            //   Pagination //  
          }
        }
        page={page}
        count={lastPage}
        onChange={onChangeCallback}
        hideNextButton
        hidePrevButton
      />

      <span className={s.text1}></span>

      <SuperSelect
        id={id + '-pagination-select'}
        value={itemsCountForPage}
        options={[
          { id: 4, value: 4 },
          { id: 7, value: 7 },
          { id: 10, value: 10 },
        ]}
        onChange={onChangeSelect}
      />

      <span className={s.text2}>  </span>
    </div>
  );
};

export default SuperPagination;
